<template>
  <md-content>
    <md-dialog ref="new_item_dialog" :md-active.sync="showDialog" :md-click-outside-to-close="false" :md-close-on-esc="false">
      <md-dialog-title>Create new item</md-dialog-title>
      <md-dialog-content>
      <!-- <header :class="activeClass" class="row">
        <div class="header-content">
          <span>{{ item.value | currency }}</span>
        </div>
        <div class="header-title">
          <div class="header-title-inner">
            {{ type }}
          </div>
        </div>
      </header> -->

        <form novalidate @submit.stop.prevent="submit">
          <md-field>
            <md-icon>today</md-icon>
            <label>Data</label>
            <md-input v-model="item.date"></md-input>
          </md-field>
          <md-field>
            <md-icon>label_outline</md-icon>
            <label>Descrição</label>
            <md-input v-model="item.description"></md-input>
          </md-field>
          <md-field>
            <md-icon>account_balance_wallet</md-icon>
            <label>Conta</label>
            <md-input v-model="item.resource.name"></md-input>
          </md-field>
          <md-field>
            <md-icon>info_outline</md-icon>
            <label>Observação</label>
            <md-input v-model="item.note"></md-input>
          </md-field>
        </form>
      </md-dialog-content>
      <md-dialog-actions>
        <md-button class="md-primary" @click.native="showDialog = false">Cancel</md-button>
        <md-button >Save</md-button>
      </md-dialog-actions>
    </md-dialog>
  </md-content>
</template>

<script>
import { defaultBill } from '../api.js'

export default {
  name: 'CreateNewItemDialog',
  data () {
    return {
      _id: null,
      item: null,
      showDialog: false
    }
  },
  mounted: function () {
    this.fetchData()
  },
  created () {
    this._id = parseInt(this.$route.params.id, 10)
    this.fetchData()
    this.byJovane()
  },
  methods: {
    fetchData () {
      this.item = defaultBill()
    },
    byJovane () {
      console.log('desenvolvido por @jovanepires')
    },
    save () {
      alert('ok')
    },
    openDialog () {
      this.showDialog = true
    }
  },
  computed: {
    activeClass: function () {
      return this.item.value >= 0 ? 'positive' : 'negative'
    },
    type: function () {
      return this.item.value >= 0 ? 'receita' : 'despesa'
    }
  }
}
</script>

<style>
  .item {
    background: #fff !important;
  }
  .main header {
    text-align: center;
    color: #fff;
    /*border-bottom: 1px solid #ededed;*/
  }
  .main header.positive {
    color: #1abc9c;
  }
  .main header.negative {
    color: #e74c3c;
  }
  .header-title {
    outline: none;
    position: relative;
    margin: 0 auto;
  }
  .header-title-inner {
    line-height: 20px;
    padding: 6px 0;
    position: relative;
  }
  .header-content {
    font-size: 25px;
    padding-top: 20px;
    position: relative;
    margin: 0 auto;
    width: 100%;
  }
</style>



